function fenlei(value) {
  let lenght = value.length;
  let jg = 0;
  if ( lenght == 15) {
    let head = value.substring(0, 6);
    let foot = value.substring(lenght - 5, lenght);
    if(/^[A-Fa-f0-9]/i.test(head.charAt(0)) && head.charAt(1) == 'A' && foot.charAt(1) =='A' && head.charAt(5) == 'X'){
        jg = {'status':200,'value':head.substring(0,2)};
    }
    else{
        jg = {'status':203,'title':"格式错误,错误类型3:代码不符合规范"}
    }
    
  } else if(lenght == 10){
    let head = value.substring(0, 6);
    console.log(head);
    if(/^[A-Fa-f0-9]/i.test(head.charAt(0)) && head.charAt(1) == 'A' &&  head.charAt(5) == 'X'){
        jg = {'status':200,'value':head.substring(0,2)}
    }
    else{
        jg = {'status':203,'title':"格式错误,错误类型3:代码不符合规范"}
    }
  }else if(lenght == 0 ){
    jg = {'status':201,'title':"格式错误,错误类型1:代码位数为空"}
  }
  else{
    jg = {'status':202,'title':"格式错误,错误类型2:代码长度错误"}
  }
  return jg;
}
export default fenlei;
